$site-base-url: "";

@import "pygments-themes.scss";

// default theme


body{
  $base-color: hsl(160, 30%, 50%); // lightness は 50% で固定
  $text-color: hsl(0, 0%, 50%);
  $palette-lightness: light;

  @import "media.scss";
  @import "palette.scss";
  @import "menu.scss";
  @import "accordion.scss";
  @import "main.scss";
  @import "syntax-highlighting.scss";
}

